body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  overflow: hidden;
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
	align-items: center;
	background: linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab);
	background-size: 400% 400%;
  -webkit-animation: gradientBG 15s ease infinite;
          animation: gradientBG 15s ease infinite;
}

@-webkit-keyframes gradientBG {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}

@keyframes gradientBG {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}

.container {
	width: 100%;
	position: absolute;
	top: 35%;
	text-align: center;
}

a,
a:hover {
	text-decoration: none;
	color: #ddd;
}

/* import Montserrat font  */

.title{
    font-family: 'Montserrat', sans-serif;
    color:#ffffff;
    font-weight: bold;
    font-size:60px;
}

.subtitle{
    font-family: 'Montserrat', sans-serif;
    color:#ffffff;
    font-weight: bold;
    font-size:25px;
}

#comeTop{
    -webkit-animation: comeTopAnimation 1s ease-in-out forwards;
            animation: comeTopAnimation 1s ease-in-out forwards; /* assign animation to monitor with 'monitorIn' as keyframe name */
    opacity: 0;
}

#comeDown{
    -webkit-animation: comeDownAnimation 1s ease-in-out forwards;
            animation: comeDownAnimation 1s ease-in-out forwards; /* assign animation to monitor with 'monitorIn' as keyframe name */
    -webkit-animation-delay:1s;
            animation-delay:1s;
    opacity: 0;
    padding-top:40px;
}

@-webkit-keyframes comeTopAnimation{
    from{
        -webkit-transform: translateY(-20px);
                transform: translateY(-20px);
        opacity: 0;
    }
    to{
        -webkit-transform: translateY(0);
                transform: translateY(0);
        opacity: 1;
    }
}

@keyframes comeTopAnimation{
    from{
        -webkit-transform: translateY(-20px);
                transform: translateY(-20px);
        opacity: 0;
    }
    to{
        -webkit-transform: translateY(0);
                transform: translateY(0);
        opacity: 1;
    }
}

@-webkit-keyframes comeDownAnimation{
    from{
        -webkit-transform: translateY(20px);
                transform: translateY(20px);
        opacity: 0;
    }
    to{
        -webkit-transform: translateY(0);
                transform: translateY(0);
        opacity: 1;
    }
}

@keyframes comeDownAnimation{
    from{
        -webkit-transform: translateY(20px);
                transform: translateY(20px);
        opacity: 0;
    }
    to{
        -webkit-transform: translateY(0);
                transform: translateY(0);
        opacity: 1;
    }
}
